
@import "../_variable.less";
@import "../_function.less";

//status rules


//widget rules

.bww-box{
	.box-sizing;
	display: flex;
	flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
	
	> .bw-vflex{
		flex:1;
	}
	&.bw-top{
		justify-content: flex-start;
	}
	&.bw-middle{
		justify-content: center;
	}
	&.bw-bottom{
		justify-content: flex-end;
	}
	&.bw-left{
		align-items: flex-start;
	}
	&.bw-center{
		align-items: center;
	}
	&.bw-right{
		align-items: flex-end;
	}	
}

.bww-hlayout{
	.box-sizing;
	display: flex;
	flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
	> .bww-hlayout-content{
		.box-sizing;
	}
	&.bw-top{
		align-items: flex-start;
	}
	&.bw-middle{
		align-items: center;
	}
	&.bw-bottom{
		align-items: flex-end;
	}
	&.bw-left{
		justify-content: flex-start;
	}
	&.bw-center{
		justify-content: center;
	}
	&.bw-right{
		justify-content: flex-end;
	}
}

.bww-vlayout{
	.box-sizing;
	display: flex;
	flex-direction: column;
	> .bww-vlayout-content{
		.box-sizing;
		// display: block;
		> .bw-vflex{
			flex:1;
		}
	}
}
